<template>
	<div>

		<el-row style="padding: 20px;">
			<el-button type="primary" @click="centerDialogVisible = true" size="medium">新建</el-button>
			<el-button type="success" @click="centerDialogVisible = true" size="medium">编辑</el-button>
			<el-button type="danger" @click="centerDialogVisible = true" size="medium">删除</el-button>
		</el-row>
		<el-table :data="tableData" border max-height="400" height="550" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column prop="date" label="日期" width="180">
			</el-table-column>
			<el-table-column prop="name" label="姓名" width="180">
			</el-table-column>
			<el-table-column prop="address" label="地址">
			</el-table-column>
			<el-table-column fixed="right" label="操作" width="180">
